<template>
  <div id="app">
    <div class="header">
      <v-header :seller='seller'></v-header>
    </div>
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import header from './components/header/header'
import goods from './components/goods/goods'
import axios from 'axios'

const ERR_OK = 0

export default {
  data() {
    return {
      seller: {}
    }
  },
  created() {
    axios.get('http://p4v6yj5ox.bkt.clouddn.com/api/seller')
      .then((response) => {
        response = response.data
        if (response.errno === ERR_OK) {
          this.seller = response.data
        }
        console.log(response.datas.supports.length)
      })
      .catch((error) => {
        console.log(error)
      })
  },
  components: {
    'v-header': header, goods
  }
}
</script>

<style lang="stylus">
  #app
    border: 1px solid red
    .tab
      display: flex
      width: 100%
      height: 40px
      line-height: 40px
      border-bottom: 1px solid rgba(7, 17, 27, 0.1)
      .tab-item
        flex: 1
        text-align: center
        font-size: 14px
        color: rgb(77, 85, 93)
        .active
          color: rgb(240, 20, 20)
</style>



